import React from 'react';
import styled from 'styled-components';
import * as toc from '../../../left-toc.json';

var demosLink = toc.demos;
var logoLink = 'https://help.syncfusion.com/';

var platformMapper = {
  'JavaScript': 'essential-js2',
  'JavaScript (ES5)': 'essential-js2',
  'Angular': 'angular',
  'React': 'react-js2',
  'Vue': 'vue',
  'ASP.NET Core': 'aspnetcore-js2',
  'ASP.NET MVC': 'aspnetmvc-js2',
  'ASP.NET Core Razor Components [Preview]': ''
};

// var aspRegex = /ASP.NET Core|ASP.NET MVC|ASP.NET Core Razor Components \[Preview\]/;

class MainHeader extends React.Component {

  /* eslint-disable */
  render() {
    const { platform } = this.props;
    return (
      <SiteContainer>
        <div className="doc-icons icon-burger_menu doc-hamburger-icon"></div>
        <a className="doc-logo-link" href={logoLink}><div className="doc-logo doc-icons icon-syncfusion"></div></a>
        <div className="doc-header-platform">{platform}</div>
        <div className={"doc-header-links" + (platform === 'ASP.NET Core Razor Components [Preview]' ? ' razor-doc-links' : '')}>
          <a href={demosLink} target="_blank"><div className="doc-header-link doc-demos-link">Demos</div></a>
          <a href="https://www.syncfusion.com/support/directtrac/incidents" target="_blank"><div className="doc-header-link doc-support-link">Support</div></a>
          <a href={`https://www.syncfusion.com/forums/${platformMapper[platform]}`} target="_blank"><div className="doc-header-link doc-forum-link">Forum</div></a>
          <a href={`https://www.syncfusion.com/downloads/${ platformMapper[platform] }`} target="_blank"><div className="doc-header-link doc-download-link">Download</div></a>
        </div>
      </SiteContainer>
    )
  }
}

const SiteContainer = styled.div`
  background: ${props => props.theme.brand};
  padding: 0px 20px;
  @media screen and (max-width: 764px) {
    padding: 0px 10px;
  }
`

export default MainHeader
