function ESTTimeOffset() {
  var jan = new Date(today.getFullYear(), 0, 1);
  var jul = new Date(today.getFullYear(), 6, 1);
  return (Math.max(jan.getTimezoneOffset(), jul.getTimezoneOffset()) <= today.getTimezoneOffset() ? -4 : -5) * 3600000;
}
// create cookie method
function createCookie(cookieName, cookieValue) {
  var d = new Date();
  if (cookieName != '_pageCount') {
    d.setMonth(d.getMonth() + 24);
    var expires = "expires=" + d.toUTCString();
  }
  document.cookie = cookieName + "=" + cookieValue + "; " + expires + "domain=.syncfusion.com;path=/";
}
//getcookie method
function getCookie(cookieName) {
  var name = cookieName + "=";
  var cookies = document.cookie.split(';');
  for (var i = 0; i < cookies.length; i++) {
    var cookie = cookies[i];
    while (cookie.charAt(0) == ' ')
      cookie = cookie.substring(1);
    if (cookie.indexOf(name) == 0)
      return cookie.substring(name.length, cookie.length);
  }
  return "";
}

function nodeSelected(args) {
  if (!args.node.querySelector('.e-icons')) {
    var path = args.nodeData.id;
    if (path.indexOf('/api/') !== -1) {
      var refPath = window.location.origin + prefix + path;
      window.open(refPath, '_blank');
    } else {
      navToPath(path);
    }
  }
}

function navToPath(navPath) {
  select('#doc-mobile-right-toc').classList.add('doc-hide');
  // var refPath = window.location.origin + prefix + navPath;
  // window.location.href = refPath;
  if(window.location.search){
    navPath = navPath + window.location.search;
  }
  navigate(navPath);
}

Array.prototype.searchArray = function (array) {
  for (var i = 0; i < this.length; i++)
    if (this[i].indexOf(array) == 0)
      return i;
  return -1;
};

function expand(e) {
  var ele = e.element.querySelector('.toc-tree');
  if (e.name === 'expanding' && ele && ele.childElementCount === 0) {
    var data = tData;
    var tree = new TreeView({
      fields: { dataSource: data[ele.id], id: 'id', text: 'name', child: 'child' },
      nodeSelected: nodeSelected
    });
    tree.appendTo(ele);
  }
}

function accClick(e) {
  var srcElem = e.originalEvent.srcElement;
  var isIcon = srcElem.classList.contains('e-icons');
  if (isApi && !isIcon && (srcElem.classList.contains('e-acrdn-header') || srcElem.classList.contains('e-acrdn-header-content'))) {
    var accItem = closest(srcElem, '.e-acrdn-item');
    var accIndElem = select('.toc-tree', accItem);
    if (accIndElem) {
      //API Index Selection
      var pathId = '/' + accIndElem.id;
      navToPath((ignoreApiSelect.indexOf(accIndElem.id) !== -1) ? (routes[routes.searchArray(pathId)]) : pathId);
    }
  } else {
    var ele = srcElem.id === 'acc-path' ? srcElem : srcElem.querySelector('#acc-path');
    if (e.name === 'clicked' && ele) {
      var aPath = ele.getAttribute('data');
      if (ele.classList.contains('doc-ref')) {
        if (isAspDocs && aPath === '/api') {
          window.open(aspApiMapper[plat], '_blank');
        } else {
          var refPath = window.location.origin + prefix + aPath;
          window.open(refPath, '_blank');
        }
      } else if (aPath) {
        navToPath(aPath);
      }
    }
  }
}
function renderToc(clearSelect) {
  let acc = new Accordion({
    expandMode: 'Single',
    expanding: expand,
    clicked: accClick,
    items: clearSelect ? window.toc.accData : aData,
    animation: { expand: { effect: 'SlideDown', duration: 0, easing: 'linear' }, collapse: { effect: 'SlideUp', duration: 400, easing: 'linear' } }
  });
  acc.appendTo('#toc');
}

function renderTab() {
  var tabs = document.getElementsByClassName('doc-tab');
  if (tabs.length) {
    for (var i = 0; i < tabs.length; i++) {
      let tab = new Tab({
        created: function () {
          var previewHeader = this.element.querySelectorAll('.e-tab-text span')[0];
          if (previewHeader.getAttribute('data-active') === 'true') {
            this.select(1);
          }
          createInnerTab(this.element);
        },
        selecting: tabSelecting,
        selected: function (args) {
          var previewContent = this.element.getElementsByClassName('e-content')[0].children[1];
          if (args.selectedIndex === 1 && !previewContent.getElementsByTagName('iframe').length) {
            var spanElement = args.selectedItem.querySelector('.e-tab-text span');

            // get iframe height
            var frameHeight = spanElement.getAttribute('data-height');
            var iframeHeight = frameHeight ? 'style="height:' + frameHeight + '"' : '';

            // create iframe for current source
            var frameSrc = spanElement.getAttribute('data-sample');
            var iframe = '<iframe class="doc-sample-frame" src="' + frameSrc + '"' + iframeHeight + '></iframe>';
            // append iframe in preview tab
            previewContent.innerHTML = iframe;
          }
        }
      });
      tab.appendTo(tabs[i]);
    }
  } else {
    createInnerTab(document);
  }

}

function createInnerTab(element) {
  var innerTabs = element.getElementsByClassName('doc-inner-tab');
  for (var i = 0; i < innerTabs.length; i++) {
    
    let innerTab = new Tab({
      selecting: tabSelecting,
      created: function (e) {
        if (plat === 'React') {
          var tbitems = this.tbItem;
          for (var k = 0, len = tbitems.length; k < len; k++) {
            var item = tbitems[k];
            var file = (window.location.search === '?type=tsx') ? '.jsx' : '.tsx';
            if (item.textContent.indexOf(file) !== -1) {
             this.hideTab(k);
            }
          }
        }
      }
    });
    innerTab.appendTo(innerTabs[i]);
  }
}
function tabSelecting(e) {
  if (e.isSwiped) {
    e.cancel = true;
  }
}

function gcseCallback() {
  if (document.readyState !== 'complete') {
    return google.setOnLoadCallback(gcseCallback, true);
  }
  var searchInput = select('#gsc-i-id1');
  if (searchInput) {
    searchInput.placeholder = 'Search by content';
    var gscSearchButton = select('button.gsc-search-button');
    detach(select('svg', gscSearchButton));
    var docSearchIcon = createElement('div', { id: 'doc-content-search-icon' });
    gscSearchButton.appendChild(docSearchIcon);
    gscSearchButton.addEventListener('click', () => {
      var searchInputEle = select('#doc-content-search input') || select('#doc-mobile-content-search input');
      if (searchInputEle.value && gcseInit) {
        gcseSearch();
      }
    })
    var searchInputEle = select('#doc-content-search input') || select('#doc-mobile-content-search input');
    searchInputEle.addEventListener('keyup', (e) => {
      if (e.keyCode === 13 && e.srcElement.value && gcseInit) {
        gcseSearch();
      }
    });
  }
};

function createCustomSearch(gcsecx) {
  if (!gcsecx.length) {
    return '';
  }
  if (isMobile) {
    select('#doc-mobile-content-search').innerHTML = select('#doc-content-search').innerHTML;
    select('#doc-content-search').innerHTML = '';
  }
  window.__gcse = {
    callback: gcseCallback
  };
  (function () {
    var gcse = document.createElement('script');
    gcse.type = 'text/javascript';
    gcse.async = true;
    gcse.src = 'https://cse.google.com/cse.js?cx=' + gcsecx;
    var s = document.getElementsByTagName('script')[0];
    s.parentNode.insertBefore(gcse, s);
  })();
}

