/* eslint no-undef: "off"*/
export const pageQuery = graphql`
  query LayoutBySlug($slug: String!) {
    allPostTitles: allMarkdownRemark{
        edges {
          node {
            fields {
              slug
              gcse
            }
          }
        }
      }
      postBySlug: markdownRemark(fields: { slug: { eq: $slug } }) {
        html
        timeToRead
        excerpt
        fields {
          slug
          gcse
        }
      } 
  }
`;
